import React, {Component} from 'react';

export default class IconArrowRight extends Component {
	render() {
		return (
			<svg width="10px" height="16px" viewBox="0 0 10 16" version="1.1" xmlns="http://www.w3.org/2000/svg" style={{
				fillRule: 'evenodd',
				clipRule: 'evenodd',
				strokeMiterlimit: 1.5,
			}}>
				<g transform="matrix(1,0,0,1,0.914859,0)">
					<path d="M0.4,15.6L7.762,7.986L0.385,0.367" style={{
						fill: 'none',
						stroke: 'rgb(54, 64, 65)',
						strokeWidth: '1px',
					}}/>
				</g>
			</svg>
		);
	}
}



