import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {Switch, Route, withRouter} from 'react-router';
import {BrowserRouter, NavLink} from 'react-router-dom';
import './app.sass';
import './parts/switch.sass';
import Header from './components/header';
import {Boost} from './connector/Boost.ts';
import {Config} from './connector/Config.ts';

import IconRekola from './apiData/rekola.jpg';
import IconTimes from './shapes/times';

// parts for router
import Login from './parts/login';
import Register from './parts/register';
import Overview from './parts/overview';
import AppBuilds from './parts/appBuilds';
import AppDetail from './parts/appDetail';
import Team from './parts/team';
import TeamItem from "./components/teamItem";
import Account from './parts/account';
import Api from './parts/api';

class App extends Component {
	constructor() {
		super();

		this.state = {
			token: localStorage.getItem('token'),
			loggedIn: true,
			route: window.location.pathname,
			menuIsVisible: false,
			icons: {},
			name: 'Boost',
			teamName: null,
			switchIsVisible: false,
			filteringIsVisible: false,
			teams: [],
			team: '',
		};

		this.config = new Config();
		this.config.onLoggedOut = this.onLoggedOut;

		this.connector = new Boost(this.config, this.state.token);

		this.changeRoute = this.changeRoute.bind(this);
		this.setToken = this.setToken.bind(this);
		this.toggleMenu = this.toggleMenu.bind(this);
		this.hideMenu = this.hideMenu.bind(this);
		this.toggleSwitch = this.toggleSwitch.bind(this);
		this.selectTeam = this.selectTeam.bind(this);
	}


	logout = () => {
		this.setToken('');
		window.location.reload();
	};

	onLoggedOut = () => {
		this.setToken('');
		window.location.reload();
	};

	componentDidMount() {
		this.connector.server()
			.then(result => {
				if (typeof result.error !== 'undefined') {
					throw new Error();
				}
				document.title = result.name;
				let icons = {};
				const head = document.querySelector('head');
				result.icons.forEach((item) => {
					icons[item.size] = item.url;
					let link = document.createElement('link');
					link.rel = 'icon';
					link.href = item.url;
					link.size = `${item.size}x${item.size}`;
					link.type = 'image/png';
					head.appendChild(link);
					// <link rel="icon" href="/favicon.ico?v=2" type="image/x-icon" />
				});
				this.setState({
					name: result.name,
					icons: icons,
				});
				if (this.state.token !== '') {
					this.connector.teams()
						.then(result => {
							console.log(result);
							this.setState({
								teams: result,
								team: result[0].id,
								teamName: result[0].name,
							});
							document.title = this.state.name + ' – ' + this.state.teamName;
						})
						.catch(error => {
							console.error(error);
						})
				}
			})
			.catch(error => {
				console.error(error);
			});
	}

	changeRoute() {
		this.setState({
			route: window.location.pathname,
		});
		console.log(this.state.route)
	}

	setToken(token) {
		localStorage.setItem('token', token);
		this.setState({
			token: token,
		});
	}

	getChildContext() {
		return {
			config: this.config,
			connector: this.connector,
			token: this.state.token || '',
			setToken: this.setToken || function() {},
			name: this.state.name,
			icons: this.state.icons,
			team: this.state.team,
		}
	}

	hideMenu() {
		if (this.state.menuIsVisible !== false) {
			this.setState({
				menuIsVisible: false
			});
		}
	}

	toggleMenu() {
		this.setState({
			menuIsVisible: !this.state.menuIsVisible
		});
	}

	toggleSwitch() {
		this.setState({
			switchIsVisible: !this.state.switchIsVisible
		});
	}

	toggleFiltering = () => {
		this.setState({
			filteringIsVisible: !this.state.filteringIsVisible,
		});
	}

	selectTeam(id, name) {
		this.setState({
			team: id,
			teamName: name,
			switchIsVisible: false,
			menuIsVisible: false,
		});
		document.title = this.state.name + ' – ' + this.state.teamName;
	}

	render() {
		return (
			<BrowserRouter onChange={this.changeRoute}>
				<div className={"outer" + (this.state.menuIsVisible ? ' is-menu' : '') + (this.state.filteringIsVisible ? ' has-filtering' : '')}>
					<div className="menu">
						<div className="menu-user">
							<div className="menu-user-icon"><img alt="Ondřej Rafaj"
																									 src="data:image/png;base64,"/>
							</div>
							<div className="menu-user-name">Ondřej Rafaj</div>
						</div>
						<ul className="menu-links">
							<li className="menu-link" onClick={this.toggleMenu}><NavLink exact activeClassName="is-active" className="menu-link-href"
																								 to={'/'}>Apps</NavLink></li>
							<li className="menu-link" onClick={this.toggleMenu}><NavLink exact activeClassName="is-active" className="menu-link-href"
																								 to={'/account'}>My account</NavLink></li>
							<li className="menu-link" onClick={this.toggleMenu}><NavLink exact activeClassName="is-active" className="menu-link-href"
																								 to={'/api'}>API keys</NavLink></li>
							<li className="menu-link" onClick={this.toggleMenu}><NavLink exact activeClassName="is-active" className="menu-link-href"
																								 to={'/team'}>Team</NavLink></li>
						</ul>
						<div className="menu-switch">
							<span onClick={this.toggleSwitch}>Switch team</span>
							<br/>
							<span onClick={this.logout}>Logout</span>
						</div>
					</div>
					<div onClick={this.hideMenu} className="inner">
						<Switch>
							<Route path='/login' exact component={null}/>
							<Route path='/register' exact component={null}/>
							<Route path='/' render={(props) => <Header {...props} icon={this.state.icons} isMoved={(this.state.menuIsVisible)} filteringIsVisible={this.state.filteringIsVisible} toggleFiltering={this.toggleFiltering} toggleMenu={this.toggleMenu}/>} />
						</Switch>
						<div className="app">
							<Route path="/login" render={(props) => <Login {...props} icon={this.state.icons}/>} onEnter={this.changeRoute}/>
							<Route path="/register" render={(props) => <Register {...props} icon={this.state.icons}/>} onEnter={this.changeRoute}/>
							<Route exact path="/team" component={Team} onEnter={this.changeRoute}/>
							<Route exact path="/account" component={Account} onEnter={this.changeRoute}/>
							<Route exact path="/api" component={Api} onEnter={this.changeRoute}/>
							<Route exact path="/" component={Overview} onEnter={this.changeRoute}/>
							<Route exact path="/app/:appId/:platform/build/:buildId" component={AppDetail}
										 onEnter={this.changeRoute}/>
							<Route exact path="/app/:appId/:platform" component={AppBuilds} onEnter={this.changeRoute}/>
						</div>
					</div>
					{
						(this.state.switchIsVisible) ?
							<div className="switch">
								<div className="switch-close" onClick={this.toggleSwitch}/>
								<div className="switch-inner">
									<div className="switch-header">
										<div className="switch-header-close" onClick={this.toggleSwitch}><IconTimes/></div>
										<div className="switch-header-title">Switch team</div>
									</div>
									{this.state.teams.map(item => <TeamItem key={item.id} id={item.id}
																													active={(this.state.team === item.id) ? true : false}
																													select={this.selectTeam} icon={IconRekola}
																													name={item.name} />)}
									<div className="switch-footer">
										<div className="switch-footer-button">+ Add new team</div>
									</div>
								</div>
							</div>
							: null
					}
				</div>
			</BrowserRouter>
		);
	}
}

App.childContextTypes = {
	config: PropTypes.object,
	connector: PropTypes.object,
	token: PropTypes.string,
	setToken: PropTypes.func,
	name: PropTypes.string,
	icons: PropTypes.object,
	team: PropTypes.string,
};

export default App;



